import React, { useState } from 'react';
import { View, AdaptivityProvider, AppRoot, Group, SplitCol, SimpleCell, Avatar, Tabs, TabsItem, ConfigProvider, HorizontalScroll, SplitLayout, ScreenSpinner } from '@vkontakte/vkui';
import { Icon16EducationOutline, Icon16UserOutline, Icon16InfoOutline } from '@vkontakte/icons';
import '@vkontakte/vkui/dist/vkui.css';

import ScheduleStudents from './panels/ScheduleStudents';
import ScheduleTeachers from './panels/ScheduleTeachers';
import Information from './panels/Information';

import logo from './images/logo.png';

const App = () => {
	const [activePanel, setActivePanel] = useState('scheduleStudents');

	return (
		<ConfigProvider>
			<AdaptivityProvider>
				<AppRoot>
      				<SplitLayout>
        				<SplitCol>
							<Group>
								<SimpleCell disabled={true} before={<Avatar src={logo} size={32}/>}>БГТУ Расписание</SimpleCell>
								<Tabs mode="accent" layoutfillmode="auto" withscrolltoselectedtab="true" scrollbehaviortoselectedtab="center">
									<HorizontalScroll arrowSize="m">
										<TabsItem
											before={<Icon16EducationOutline/>}
											selected={activePanel === 'scheduleStudents'}
											onClick={() => setActivePanel('scheduleStudents')}
										>Расписание студентов</TabsItem>
										<TabsItem
											before={<Icon16UserOutline/>}
											selected={activePanel === 'scheduleTeachers'}
											onClick={() => setActivePanel('scheduleTeachers')}
										>Расписание преподавателей</TabsItem>
										<TabsItem
											before={<Icon16InfoOutline/>}
											selected={activePanel === 'information'}
											onClick={() => setActivePanel('information')}
										>О приложении</TabsItem>
									</HorizontalScroll>
								</Tabs>
							</Group>
							<ConfigProvider transitionMotionEnabled={false}>
								<View activePanel={activePanel}>
									<ScheduleStudents id="scheduleStudents" />
									<ScheduleTeachers id="scheduleTeachers" />
									<Information id="information" />
								</View>
							</ConfigProvider>
       					</SplitCol>
      				</SplitLayout>
    			</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}

export default App;
