import React from 'react';
import { Panel, Group, Header, SimpleCell, InfoRow } from '@vkontakte/vkui';
import { lastStudentsScheduleUpdate, lastTeachersScheduleUpdate } from '../Database';

const Information = props => {
    return (
        <Panel id={props.id}>
            <Group header={<Header mode="secondary">Техническая информация</Header>}>
                <SimpleCell multiline disabled={true}>
                    <InfoRow header="Версия приложения">1.1.1</InfoRow>
                </SimpleCell>
                <SimpleCell multiline disabled={true}>
                    <InfoRow header="Последнее обновление расписания студентов">{lastStudentsScheduleUpdate()}</InfoRow>
                </SimpleCell>
                <SimpleCell multiline disabled={true}>
                    <InfoRow header="Последнее обновление расписания преподавателей">{lastTeachersScheduleUpdate()}</InfoRow>
                </SimpleCell>
            </Group>
        </Panel>
    );
}

export default Information;