import React, { useState, useEffect } from 'react';
import bridge from "@vkontakte/vk-bridge";
import { MiniInfoCell, Panel, Select, FormItem, CustomSelect, Card, Header, Group, CardGrid, Separator, Spacing, RichCell } from '@vkontakte/vkui';
import { Icon20CalendarOutline, Icon20EducationOutline } from '@vkontakte/icons';
import { getWeekStatus, currentTeachersPeriod, findTeachers, findTeachersSchedule, getFaculties } from '../Database';

const ScheduleTeachers = props => {
    const [faculty, setFaculty] = useState();
    const [teacher, setTeacher] = useState();

    function storageSet(value) {
        bridge.send('VKWebAppStorageSet', { 
            key: 'scheduleTeachers',
            value: JSON.stringify([value]),
        });
    }

    useEffect(() => {
        bridge
            .send('VKWebAppStorageGet', { keys: ['scheduleTeachers']})
            .then((data) => {
                if (data.keys) {
                    setTeacher(JSON.parse(data.keys[0].value)[0]);
                }
            });
    }, []);

    let teacherData = findTeachersSchedule(teacher);

    return (
        <Panel id={props.id}>
            <Group>
                <MiniInfoCell mode="accent" before={<Icon20CalendarOutline/>}>{getWeekStatus()}</MiniInfoCell>
                <MiniInfoCell mode="accent" before={<Icon20EducationOutline/>}>Текущий период: {currentTeachersPeriod()}</MiniInfoCell>
            </Group>
            <Group>
                <FormItem top="Факультет / Институт">
                    <Select multiline
                        value={faculty}
                        onChange={(e) => setFaculty(e.target.value)}
                        placeholder="Выберите факультет или институт"
                        options={getFaculties()}
                    />
                </FormItem>
                <FormItem top="Преподаватель">
                    <CustomSelect multiline="true"
                        searchable
                        value={teacher}
                        onChange={(e) => {
                            setTeacher(e.target.value);
                            storageSet(e.target.value);
                        }}
                        placeholder="Введите ФИО преподавателя"
                        options={findTeachers(faculty)}
                    />
                </FormItem>
            </Group>
            {teacherData && Object.keys(teacherData.schedule).map((day) => (
                <Group header={<Header mode="primary" size="large">{day}</Header>}>
                    <CardGrid size="l">
                        {Object.keys(teacherData.schedule[day]).map((lessons) => (
                            <Card mode="shadow" >
                                {teacherData.schedule[day][lessons].week==="Всегда" && (
                                    <RichCell multiline
                                        disabled={true}
                                        before={teacherData.schedule[day][lessons].time}
                                        text={teacherData.schedule[day][lessons].type}
                                        caption={teacherData.schedule[day][lessons].group}
                                        after={teacherData.schedule[day][lessons].classroom}
                                    >{teacherData.schedule[day][lessons].title}</RichCell>
                                )}
                                {teacherData.schedule[day][lessons].week==="Чётная" && (
                                    <RichCell multiline
                                        disabled={true}
                                        before={teacherData.schedule[day][lessons-1].time}
                                        text={teacherData.schedule[day][lessons-1].type}
                                        caption={teacherData.schedule[day][lessons-1].group}
                                        after={teacherData.schedule[day][lessons-1].classroom}
                                    >{teacherData.schedule[day][lessons-1].title}</RichCell>
                                )}
                                {teacherData.schedule[day][lessons].week==="Чётная" && (
                                    <Spacing size={12}>
                                        <Separator />
                                    </Spacing>
                                )}                             
                                {teacherData.schedule[day][lessons].week==="Чётная" && (
                                    <RichCell multiline
                                        disabled={true}
                                        before={teacherData.schedule[day][lessons].time}
                                        text={teacherData.schedule[day][lessons].type}
                                        caption={teacherData.schedule[day][lessons].group}
                                        after={teacherData.schedule[day][lessons].classroom}
                                    >{teacherData.schedule[day][lessons].title}</RichCell>
                                )}
                            </Card>
                        ))}
                    </CardGrid>
                </Group>
            ))}
        </Panel>
    );
}

export default ScheduleTeachers;